
.weui-cells{
  font-size: 14px !important;
  margin-top: 0 !important;
}
.weui-cell{
  padding: 15px 12px !important;
}
.weui-cell:before,.vux-cell-box:before{
  border-color: #e5e5e5 !important;
}
.vux-cell-box .weui-cell_access{
  padding: 0px 12px 0px 12px !important;
}
.vux-popup-picker-value{
  padding: 8px;
  text-decoration: none;
  color: #fff;
  border-radius: 3px;
  background-color: #9ccc66;
  margin-right: 7px;
  margin-top: 7px;
  /*margin-bottom: 7px;*/
  line-height: 20px;
  display: inline-block;
}
.vux-cell-box .weui-cell_access .vux-cell-primary{
  margin-bottom: 7px;
}
/*.weui-cells:before {
  border-top: none !important;
}*/
.prompt{
  font-size: 13px;
  padding: 10px 12px;
  color: #999997;
}
.vux-cell-box .weui-cell_access .weui-cell__hd{
  padding: 15px 0;
}
