/*提交按钮*/
.sub_btn{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9;
  background-color: #fff;
  /*box-shadow: 0px 12px 12px 9px #888888;*/
}
.sub_btn:before{
  content: " ";
  border-top: 1px solid #D9D9D9;
  display: block;
  -webkit-transform: scaleY(0.5);
  transform:scaleY(0.5);
}
.sub_btn .weui-btn_primary{
  background-color: transparent;
  color: #2db255;
}
.sub_btn .weui-btn:after{
  border: none;
}

/*编辑按钮*/
.edit_btn{
  width: 50px;
  height: 50px;
  background:url(../../assets/edit_icon.png) no-repeat;
  background-size: 100%;
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 10;
}
.edit_btn:hover{
  background:url(../../assets/edit_hover.png) no-repeat;
  background-size: 100%;
}
