
.checker_cont{
  padding: 10px;
  min-height: 100px;
  background-color: #fff;
}
.def_checker{
  border: 1px solid #ececec;
  padding: 3px 12px;
  margin-right: 6px;
  margin-bottom: 6px;
  border-radius: 3px;
  font-size: 14px;
}
.selected_checker{
  border: 1px solid green;
}
.weui-cells_checkbox {
  background-color: #f5f5f5;
}



